.projectsPage {
    text-align: center;
    padding: 20px 0;
}




.projectTile {
    height: 240px;
    width: 240px;
    display: inline-block;
    vertical-align: top;
    /* width: calc(100% - 20px); */
    /*border: 1px solid;*/
    margin: 20px 30px;
     -webkit-box-shadow: -1px 1px 0px 2px rgba(0,0,0,.5);
             box-shadow: -1px 1px 0px 2px rgba(0,0,0,.5);  
    text-align: left;
    /* padding: 20px; */
    font-family: Ubuntu, sans-serif; 
    position: relative;
    /* Rectangle 4: */
    background: #FFFFFF;
    /* box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50); */
    /* overflow: hidden; */
}

/* .projectTile:hover .projectTileLabel {
    opacity: 1;
    background-color: rgba(0,0,0,.75);
    color: #fff;
} */

.projectTileLabel {
     position: absolute; 
     bottom: 10px; 
     left: 10px; 
    /* height: 100%; */
    width: calc(100% - 20px);
    /* padding: 20px; */
    /* z-index: 5; */
    /* opacity: 0; */
    height: 30px;
    line-height: 30px;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    /* padding: 10px; */
}

.projectTileImage {
     position: absolute; 
     top: 12px; 
     left: 12px; 
    height: 180px;
    width: 240px;
    overflow: hidden;
    background: #FFFFFF;    
    /* Rectangle 4: */
    /* box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50); */
    -webkit-box-shadow: 0 0 0 1px #000;
            box-shadow: 0 0 0 1px #000;

    /* background-color: rgba(0,0,0,.5); */
}

.projectTileImage img {
    height: 100%;
    width: auto;
    margin: auto;
}










.daysOfArt {

}

.pageHeader {
    width: 100%;
    text-align: center;
    padding: 20px;
    font-size: 2rem;
}

.daysOfArtDescription {
    margin: 0 5% 20px 5%;
}

.backButton {
    display: block;
    margin: 20px;
}

.dayArtImage {
    display: -ms-inline-flexbox;
    display: inline-flex;
    vertical-align: top;
    -ms-flex-pack: center;
        justify-content: center;
    height: 300px;
    width: 300px;
    margin: 10px;
    overflow: hidden;
}

.dayArtImage img {
    height: 100%;
    width: auto;
}




.ieeeConnectPage {
    height: auto;
    width: 100%;
    text-align: left;
}

.ieeeConnectPage .ieeeConnectDescription {
    margin: 0 5%;
    color: #666;
}

.ieeeConnectPage .bottomMargin {
    margin-bottom: 20px;
}

.appImagesContainer {
    width: 100%;
    overflow: hidden;
    padding: 20px 0;
}

.appImagesContainer img {
    width: 100%;
    height: auto;
    float: left;
}


.credenz14page {
    height: auto;
    width: 100%;
    text-align: center;
}

.credenz14page img {
    max-width: 100%;
    height: auto;
    margin: 5px 0;
    /* float: left; */
}

.credenz14page .credenzDescription {
    text-align: left;
    margin: 0 5%;
    color: #666;
}



.backButton {
    position: fixed;
    bottom: 20px;
    left: 20px;
    height: 40px;
    width: 40px;
    border: 1px solid black;
}


